$default_font: 'Viga', sans-serif;

.hidden {
  display: none !important;
}

::-webkit-scrollbar {
  /* 1 - скроллбар */
  width: 10px;
  height: 4px;
  background-color: none;
  margin-right: 10px;
}

::-webkit-scrollbar-thumb {
  /* 5 - ползунок */
  border-radius: 12px;
  background-color: #C4C4C4;
  padding-right: 10px;
}

body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

#root {
  width: 100%;
  height: 100%;
}

@keyframes in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes out {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

