/// slider
.container-slider {
  width: 100vw;
  height: 150vh;
  position: absolute;
  overflow: hidden;
  z-index: -2;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);

  .slide {
    width: 100%;
    height: 100%;
    position: absolute;
    opacity: 0;
    transition: opacity ease-in-out 0.4s;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .active-anim {
    opacity: 1;
  }
}

// slider

.preloader {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: white;
  z-index: 999;
  transition: 1s all;
  opacity: 1;
  visibility: visible;

  @keyframes spin {
    from {
      transform: translate(-50%, -50%) rotate(0deg);
    }

    to {
      transform: translate(-50%, -50%) rotate(360deg);
    }
  }

  .loader {
    width: 75px;
    height: 75px;
    border: 10px solid white;
    border-radius: 50%;
    border-top-color: rgb(173, 111, 28);
    background: #000;
    left: 50%;
    top: 50%;
    position: absolute;
    transform: translate(-50%, -50%);
    animation: 1s spin infinite linear;
  }

  &.done {
    opacity: 0;
    visibility: hidden;
  }
}


header {
  cursor: pointer;
  width: 75vw;
  height: 100px;
  background-color: inherit;
  border-bottom: 0.31px solid white;
  border-color: rgb(197, 195, 195);
  display: flex;
  position: absolute;
  top: 0;
  left: 13%;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  font-size: 18px;
  margin-left: auto;
  margin-right: auto;
  // color: rgb(228, 222, 222);
  color: white;
  align-items: center;
  opacity: 0.9;
  z-index: 1;

  .contact-info {
    width: 8vw;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border-bottom: 1px solid transparent;
    a{
      text-decoration: none;
      color: rgb(255, 255, 255);
      &:hover{
        border-bottom: 1px solid white;
      }
    }
  }

  .authorize {
    display: flex;
    width: 4vw;
    justify-content: space-between;
    align-items: center;
    opacity: 1;
    transition: 0.5s linear;

    &:hover {
      transition: 0.5s linear;
      opacity: 0.5;

    }
  }

  .modal {
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.6);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
    z-index: 1;
  }

  .modal_active {
    opacity: 1;
    pointer-events: all;
  }

  .modal__contant_active {
    transform: scale(1);
  }

  .modal__contant {
    padding: 40px;
    border-radius: 12px;
    transform: scale(0.5);
    width: 50vw;
    height: 100%;
    background: rgba(249, 249, 249, 0.9);
    transition: 0.4s all;
    font-size: 40px;
    color: #da5d37;
    display: flex;
    justify-content: center;
    align-items: center;

    .purchased_order {
      display: flex;
      flex-direction: column;

      input {
        margin-bottom: 30px;
        font-size: 30px;
        padding: 6px 0 4px 10px;
        border: 1px solid #ba0909;
        background: #F6F6f6;
        border-radius: 8px;

        &:checked {
          border: 1px solid #da5d37;
        }
      }
    }

    .Custom_Button {
      border: 1px solid;
      background-color: transparent;
      border-radius: 10px;
      text-transform: uppercase;
      font-size: 16px;
      padding: 15px 30px;
      font-weight: 500;
      transition: 0.2s;
      color: rgb(45, 10, 159);
      cursor: pointer;

      &:hover {
        color: white;
        border: 0;
        background-color: #0a10bf;
        box-shadow: 0px 0px 80px 6px #23068c;
      }
    }

    .error {
      font-size: 24px;
      color: red;
    }

    .login {
      cursor: default;
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .logo {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;

        img {
          width: 250px;
        }

        hr {
          background-image: linear-gradient(to right, transparent, rgba(0, 0, 0, 0.2), transparent);
          border: 0;
          height: 3px;
          width: 100%;
          margin-top: 120px;
          display: block;
        }
      }

      .text {
        color: #2e3645;
        font-size: 35px;
        text-align: center;
      }

      form {
        display: flex;
        flex-direction: column;
        width: 40%;
        margin: 0 auto;
        margin-top: 20px;
        text-align: center;

        .inputlogin {
          width: 100%;
          height: 40px;
          font-size: 30px;
          border-radius: 10px;
          border: 0;
          margin-bottom: 20px;
          padding: 2px 5px;

          &:focus {
            box-shadow: none;
            border: none;
          }
        }

        .inputpassword {
          width: 100%;
          height: 40px;
          font-size: 30px;
          border-radius: 10px;
          border: 0;
          margin-bottom: 20px;
          padding: 2px 5px;

          &:focus {
            box-shadow: none;
            border: none;
          }
        }

        .inputerror {
          border: 1px solid red;
        }

        .error {
          font-size: 24px;
          color: red;
        }
      }

      .button {
        display: flex;
        justify-content: center;
        margin-top: 30px;

        button {
          background-color: green;
          height: 50px;
          width: 150px;
          font-size: 40px;
          border: 0;
          border-radius: 10px;
          color: white;
          cursor: pointer;
          text-align: center;
          margin-left: 20px;
        }

      }
    }
  }
}

