          .room {
            margin: 0;
            padding: 20px 0px;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            width: 100%;
            flex-wrap: wrap;

            li {
              width: 400px;
              padding: 0px 0px 0px 0px;
              margin-top: 10px;
              list-style-type: none;
              border: 1px solid white;
              display: flex;
              flex-direction: column;
              align-items: center;

              .properties {
                margin: 0;

                .properties-li {
                  border: 0;
                  height: 30px;
