              .button-container {
                display: flex;
                width: 100%;
                align-items: center;
                justify-content: center;
                overflow: hidden;
                background: rgb(255, 255, 255);
                color: #000;
                cursor: pointer;

                &:hover {
                  .price {
                    opacity: 0;
                  }

                  .button {
                    opacity: 1;
                  }
                }

                .price {
                  font-size: 20px;
                  transition: 1s;
                  white-space: nowrap;
                  opacity: 1;
                  position: absolute;
                  z-index: 0;
                }

                .button {
                  padding: 20px 0;
                  width: 100%;
                  line-height: 100%;
                  text-align: center;
                  vertical-align: middle;
                  transition: 1s;
                  font-size: 20px;
                  opacity: 0;
                  z-index: 1;
