              transition: 1.5s all;


              .slide {
                width: 100%;
                height: 100%;
                position: absolute;
                opacity: 0;
                transition: opacity ease-in-out 0.4s;

                img {
                  width: 100%;
                  height: 100%;
                  object-fit: cover;
                }

                &.active-anim {
                  opacity: 1;
                }
              }

              .container-dots {
                position: absolute;
                bottom: 10px;
                left: 50%;
                transform: translateX(-50%);
                display: flex;

                .dot {
                  width: 15px;
                  height: 15px;
                  border-radius: 50%;
                  border: 3px solid #000000;
                  margin: 0 5px;
                  background: #f1f1f1;

                  &.active {
                    background: rgb(32, 32, 32);
                  }
                }
              }
            }
          }

