                  font-size: 20px;
                  opacity: 0;
                  z-index: 1;
                  border: 3px solid white;

                  &.active {
                    opacity: 1;
                    border-radius: 3px;
                    background-color: white;
                    border: 3px solid rgb(0, 255, 0);
                    color: rgb(0, 0, 0);
                    z-index: 1;
                  }
                }
