                flex-direction: column;
                align-items: center;
                justify-content: space-between;

                .name {
                  font-size: 26px;
                  padding: 10px;
                }

                .properties {
                  margin: 0;
                  padding: 10px;

                  .properties-li {
                    border: 0;
                    width: 100%;

                    font-size: 19px;
                    display: flex;
                    flex-direction: row;
                    padding: 10px 0;
                    text-align: left;
                    margin: 0;
                  }
                }

                .button-container {
                  display: flex;
                  width: 100%;
                  align-items: center;
                  justify-content: center;
                  overflow: hidden;
                  background: rgb(255, 255, 255);
                  color: #000;
                  cursor: pointer;

                  &:hover {
                    .price {
                      opacity: 0;
                    }

                    .button {
                      opacity: 1;
                    }
                  }

                  .price {
                    font-size: 20px;
                    transition: 1s;
                    white-space: nowrap;
                    opacity: 1;
                    position: absolute;
                    z-index: 0;
                  }

                  .button {
                    padding: 20px 0;
                    width: 100%;
                    line-height: 100%;
                    text-align: center;
                    vertical-align: middle;
