                    z-index: 1;
                    border: 3px solid rgb(0, 255, 0);

                    &.active {
                      padding: 20px 0;
                      opacity: 1;
                      border: 3px solid rgb(0, 255, 0);
                      background-color: rgb(255, 255, 255);
                      color: rgb(0, 0, 0);
                      z-index: 1;
                    }
                  }
                }
              }
            }
          }

          .result {
            padding: 20px 0;
            display: flex;
            flex-direction: row;
            justify-content: space-around;
            flex-wrap: wrap;
            align-items: center;

            .result_info {
              display: flex;
              flex-direction: column;
              justify-content: space-between;
              border: 1px solid white;
              overflow: hidden;
              border-radius: 10px;
              padding: 20px 0px 0px 0px;

              table {
                border-spacing: 0px;
                padding: 20px 40px;

                th {
                  text-align: center;
                  padding: 10px;
                  border: 1px solid white;
                  font-size: 20px;
                }

                td {
                  font-size: 18px;
                  padding: 10px;
                  border: 1px solid white;
                }
              }

              h1 {
                margin: 0 auto;
                margin-bottom: 20px;
                padding: 0;
                padding-bottom: 20px;
                border-bottom: 1px solid;
                text-align: center;
                align-items: center;
                width: 100%;
              }

              .end {
                border: 0;
                margin: 0;
                padding: 10px 0;
                background-color: rgba(13, 245, 13, 0.7);
              }

              .date {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                padding: 20px 50px;

                .to {
                  display: flex;
                  flex-direction: column;
                  align-items: center;
                  justify-content: space-between;
                  height: 50px;

                  .name {
                    font-size: 20px;
                  }

                  .info {
                    font-size: 18px;
                  }
                }

                .from {
                  @extend .to;
                }

              }

            }

            .form {
              @extend .result_info;

              .end {
                cursor: pointer;
              }

              .data {
                display: flex;
                flex-direction: column;
                padding: 20px 50px 20px 30px;

                .error {
                  border: 1px solid rgb(255, 9, 9);
                }

                input {
                  width: 100%;
                  outline: none;
                  border: 1px solid white;
                  border-radius: 5px;
                  margin-bottom: 25px;
                  font-size: 20px;
                  padding: 10px;
                }
              }

              h3 {
                font-size: 14px;
                padding: 0px 50px;
                text-align: center;
              }
            }
          }
