  .modal {
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.6);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
  }

  .modal_active {
    opacity: 1;
    pointer-events: all;
  }

  .modal__contant_active {
    transform: scale(1);
  }

  .modal__contant {
    padding: 40px;
    border-radius: 12px;
    transform: scale(0.5);
    background: rgba(249, 249, 249, 0.9);
    transition: 0.4s all;
    font-size: 40px;
    color: #da5d37;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;

    .purchased_order {
      display: flex;
      flex-direction: column;

      label {
        color: #868e8e;
      }

      input {
        margin-bottom: 30px;
        font-size: 30px;
        padding: 6px 0 4px 10px;
        border: 1px solid #8b8b8b;
        background: #F6F6f6;
        border-radius: 8px;

        &:checked {
          border: 1px solid #da5d37;
        }
      }
    }

    .Custom_Button {
      border: 1px solid;
      background-color: transparent;
      border-radius: 10px;
      text-transform: uppercase;
      font-size: 16px;
      padding: 15px 30px;
      font-weight: 500;
      transition: 0.2s;
      color: rgb(45, 10, 159);
      cursor: pointer;

      &:hover {
        color: white;
        border: 0;
        background-color: #0a10bf;
        box-shadow: 0px 0px 80px 6px #23068c;
      }
    }
  }
}

