}

.egipt_container {
  margin-top: 200px;
  margin-bottom: 176px;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap;
  margin-left: auto;
  width: 88vw;
  z-index: 1;

  .main-text {
    z-index: 0;

    h1 {
      font-size: calc(1em + 3vw);
      width: 900px;
      margin-bottom: 40px;
      word-wrap: break-word;

      font-weight: lighter;
      opacity: 1;
    }

    h3 {
      opacity: 1;
      margin-top: 0px;
      font-size: calc(1em + 1vw);
      width: 700px;
      font-weight: 100;
      letter-spacing: 2px;
    }

    @keyframes slidein {
      from {
        top: -100px;
        opacity: 0;
      }

      to {
        top: 53px;
        opacity: 1;
      }

    }

    .CreateOrder {
      width: 30em;
      height: 150px;
      backdrop-filter: blur(100px);
      display: flex;
      border-radius: 20px;
      justify-content: space-between;
      align-items: center;

      .CreateOrder_container {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        padding-left: 20px;

        .text {
          font-size: 18px;
          font-style: italic;
          margin-bottom: 10px;
        }

        .contry {
          width: 15em;

          .select-Countrys {
            width: 100%;
            border: 1px solid rgb(255, 255, 255);
            position: relative;
            display: flex;
            padding: 10px;
            border-radius: 10px;
            flex-direction: row;

            .select-header-Countrys {
              width: 100%;
              display: flex;
              flex-direction: row;
              justify-content: space-between;
              align-items: center;

              .current-container-Countrys {
                display: flex;
                flex-direction: column;

                .text {
                  margin: 0;
                }

                .select_current-Countrys {
                  font-size: 16px;
                  margin-top: 8px;
                  font-weight: lighter;
                }
              }

              .select_icon {
                align-items: center;
                display: flex;
                flex-shrink: 0;
                justify-content: center;
                text-align: center;
                margin-left: auto;
                transition: 0.5s;
              }
            }

            .select_body-Countrys {
              animation: slidein 0.5s alternate;
              transition: 0.5s;
              border-radius: 10px;
              left: 0;
              right: 0;
              background-color: transparent;
              position: absolute;
              top: 53px;
              overflow-y: scroll;
              height: 150px;
              padding: 0;

              .select_item {
                list-style-type: none;
                line-height: 24px;
                font-size: 16px;
                padding: 8px;
                padding-left: 20px;
                border-bottom: 1px solid #f0f0f0;

                cursor: pointer;

                &:hover {
                  color: green;
                  background-color: white;
                }
              }
            }
          }
        }
      }

      .Custom_Button {
        border: 1px solid rgb(160, 158, 158);
