        border-radius: 10px;
        margin-right: 30px;
        font-size: 16px;
        padding: 15px 30px;
        font-weight: 500;
        transition: 0.2s;
        color: rgb(160, 158, 158);
        cursor: pointer;

        &:hover {
          color: rgb(239, 232, 32);
          border: 1px solid rgb(239, 232, 32);
        }
      }
    }
  }

  .nextPage {
    display: flex;
    width: 35%;
    height: 600px;
    z-index: -1;
    border: 1px solid white;
    border-top-left-radius: 16px;
    border-bottom-left-radius: 16px;

    .small-container-slider {
      width: 50%;
      overflow: hidden;
